// auth.service.ts
import { Injectable } from '@angular/core';
import { UsersService } from './users.service';
import { catchError, map, Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private readonly ACCESS_TOKEN_KEY = 'accessToken';
  private readonly REFRESH_TOKEN_KEY = 'refreshToken';

  constructor(private usersService: UsersService) {}

  // Sauvegarder les deux tokens
  saveTokens(accessToken: string, refreshToken: string): void {
    localStorage.setItem('accessToken', accessToken);
    localStorage.setItem('refreshToken', refreshToken);
  }
  saveAccessToken(token: string): void {
    localStorage.setItem('accessToken', token);
  }

  // Rcuprer l'accessToken
  getAccessToken(): void {
    this.usersService.getValidAccessToken().subscribe({
      next: (token) => {
        console.log(' Token lu depuis localStorage (async) :', token);
        // utilise le token ici
      },
      error: (err) => {
        console.error('Erreur lors de la rcupration du token :', err);
      }
    });
  }

  public getToken(): string | null {
    return localStorage.getItem(this.ACCESS_TOKEN_KEY);
  }

  public fetchAccessToken(): Observable<string> {
    return this.usersService.getValidAccessToken();
  }

  // Rcuprer le refreshToken
  getRefreshToken(): string | null {
    return localStorage.getItem('refreshToken');
  }

  // Supprimer les tokens  la dconnexion
  removeTokens(): void {
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
  }

  // Mthode d'authentification simple
  isAuthenticated(): Observable<boolean> {
    return this.usersService.getValidAccessToken().pipe(
      map(token => !!token),
      catchError(() => of(false))
    );
  }


  isLoggedIn(): Observable<boolean> {
    return this.isAuthenticated();
  }


  // public getCurrentUserToken(): Observable<string | null> {
  //   // on rcupre le mme champ que dans saveTokens()
  //   return of(localStorage.getItem(this.ACCESS_TOKEN_KEY));
  // }
  
  public getCurrentUserToken(): Observable<string> {
    const token = localStorage.getItem(this.ACCESS_TOKEN_KEY);
    if (!token) {
      // soit on propage une erreur :
      throw new Error('Aucun token trouv en localStorage');
      //  ou bien on renvoie une chane vide pour viter le null :
      // return of('');
    }
    return of(token);
  }


  // ici
  // 
  
}

